import React, {useState} from 'react'
import {animated, useSpring} from 'react-spring'
import * as easings from 'd3-ease'
import ProjOneBack from './proj-one.png'
import ProjTwoBack from './proj-two.png'
import PageNav from './pageNav'

const WorkSection = ({fontSize, page, prevPage, nextPage, Works}) => {
    const WorkImage = [ProjOneBack, ProjTwoBack, ProjOneBack, ProjTwoBack]

    const [hoverIndex, setHoverIndex] = useState(null)

    const SectionAnimate = useSpring({
        from: {
            transform: 'translateX(-100vw)',
            opacity: 0
        },
        to: {
            transform: (page === "Works")?'translateX(0vw)':'translateX(-100vw)',
            opacity: (page === "Works")?1:0
        },
        delay: 150,
        config: {
            duration: 1200,
            easing: easings.easeCubic
        }
    })

    const ImageAnimate = (index) => useSpring({
        from: {
            width: 0,
            opacity: 0,
            height: (24*fontSize)
        },
        to: {
            width: (index === hoverIndex)?(32*fontSize):0,
            opacity: (index === hoverIndex)?1:0,
            height: (24*fontSize)
        },
        config: {
            duration: 450,
            easing: easings.easeCubicInOut
        }
    })    
    
    const HeaderAnimate = (index) => useSpring({
        from: {
            marginLeft: -10,
            opacity: 0
        },
        to: {
            marginLeft: (page === "Works")?0:-10,
            opacity: (page === "Works")?1:0 
        },
        delay: 1400 + (index*150),
        config: {
            duration: 450,
            easing: easings.easeCubic
        }
    })

    const openItem = () => {
        nextPage("Item")
    }

    return (
        <animated.div className="project-section" style={SectionAnimate}>
            <PageNav prevPage={prevPage} page={page} pageName={"Works"}/>
            <div className="project-item-section">
            {Works.map((item, index) => (
                <animated.div className="project-item" style={HeaderAnimate(index)} onClick={openItem}>
                    <h1 onMouseEnter={() => setHoverIndex(index)} onMouseLeave={() => setHoverIndex(null)}>{item}</h1>
                    <p>quisque egestas diam in arcu cursus euismod quis viverra nibh</p>
                    <animated.img src={WorkImage[index]} alt={`${item} Demo`} style={ImageAnimate(index)}/>
                </animated.div>
            ))}
            </div>
        </animated.div>
    )
}

export default WorkSection