import { animated, useSpring } from 'react-spring'
import * as easings from 'd3-ease'

const PageNav = ({prevPage, page, pageName, color}) => {

    const BurgerAnimate = () => useSpring({
        from: {
            opacity: 0
        },
        to: {
            opacity: (page === pageName)?1:0
        },
        delay: 1200,
        config: {
            duration: 450,
            easing: easings.easeCubicInOut
        }
    })

    const textColor = () => {
        var textClass = ""
        if(page === "Item"){
            textClass = color
        } 
        return textClass
    }

    return (        
        <animated.div className={`section-burger ${textColor()}`} onClick={() => prevPage()} style={BurgerAnimate()}>
            <div className="line line1"></div>
            <div className="line line2"></div>
        </animated.div>
    )
}

export default PageNav